	module.exports = {"data":{"wordpressPost":{"title":"BMJ Open and BMJ articles on device recalls","content":"<p>Yesterday BMJ Open published <a href=\"http://bmjopen.bmj.com/content/early/2011/05/12/bmjopen-2011-000155.full\">&#8216;Medical-device recalls in the UK and the device-regulation process: retrospective review of safety notices and alerts</a>&#8216;.</p>\n<p>This paper sits with features, analysis and commentary in the BMJ and the latest edition of Dispatches, ‘<a href=\"http://www.channel4.com/programmes/dispatches/episode-guide/series-90/episode-1\">The truth about going under the knife</a>’, on Channel 4 (tonight, 16 May, 8 pm). The full list of BMJ/BMJ Open articles is below.</p>\n<p>The BMJ Open article concludes that as recalls of medical devices in the UK are on the increase, the lack of access to transparent data and a registry of highest-risk devices is a major problem, with serious adverse effects for patients and increased healthcare costs the potential consequences.</p>\n<ul>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2905\">Feature: Out of joint</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2748\">Feature: Europeans are left to their own devices</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2973\">Analysis: Medical device recalls and transparency in the UK</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2822\">Editorial: The regulation of medical devices</a></li>\n<li><a href=\"http://bmjopen.bmj.com/content/early/2011/05/12/bmjopen-2011-000155.short?rss=1\">Research from BMJ Open: Medical device recalls in the UK and the device regulation process</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2839\">Commentary: Evaluating and regulating device therapy</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2826\">Commentary: Use of registries to investigate the past and develop the future</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d2952\">Commentary: International collaboration needed on device clinical standards</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d3009\">Commentary: Metal on metal hips</a></li>\n<li><a href=\"http://www.bmj.com/cgi/doi/10.1136/bmj.d3021\">Commentary: The risk of over-regulation</a></li>\n</ul>\n<p><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"e53dffb6-af8f-5b27-b421-3dbe20980102"}}

